.CreationModal input {
  padding: 10px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
  width: 100%;
}

.CreationModal input::placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

.CreationModal input.error {
  border: 1px solid red;
}

.CreationModal .errorMessage {
  color: red;
  font-size: 0.85em;
  display: relative;
}
