.field-width {
  width: 45%;
  border-radius: 5px;
  margin: 1%;
  border: 1px solid #cfcfcf;
}
.field-width-label {
  width: 45%;
  border-radius: 5px;
  margin: 1%;
  border: 1px solid #cfcfcf;
  background-color: #519e8a;
  color: white;
  padding: 10px;
  font-size: 15px;
  text-align: center;
}
.campaign-form-wrapper {
  width: 600px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0px 10px 50px #555;
  background-color: #ffffff;
}
.campaign-form {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.campaign-form input {
  padding: 10px 7px;
  outline: none;
  font-size: 1rem;
  width: 100%;
  border: none;
}
.campaign-form input::placeholder {
  font-size: 1rem;
  font-weight: light;
  color: #000;
}
