.tabbutton {
  display: inline-block;
  background-color: inherit;
  border: 1px solid #ccc;
  color: black;
  padding: 22px 16px;
  width: 100%;
  margin: 1%;
  outline: none;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tabbutton:hover {
  background-color: #ddd;
}

/* Create an active/current "tab button" class */
.tabbutton.active {
  background-color: #ccc;
}
