*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  box-sizing: border-box;
  margin: 10px;
  color: #081f2c;
  font: 400 13px/20px "Source Sans Pro", sans-serif;
}
svg {
  display: block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}
.select {
  position: relative;
  display: inline-block;
  width: 200px !important;
  border: 2px solid #7988a4;
  color: #7988a4 !important;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
}
.assignButton {
  position: relative;
  display: inline-block;
  width: 21% !important;
  padding: 1% 1%;
  border: 2px solid darkblue;
  border-radius: 5px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  background-color: #7888a4;
  margin: 0% 1%;
}
.select:focus {
  outline: none;
}
.selection {
  position: relative;
  padding: 5px;
  border: 1px solid #ddd;
  background: #fff;
}
.selection-assignButton {
  position: relative;
}
.value {
  position: relative;
  display: inline-block;
  padding: 5px 5px;
}
.multiple {
  padding-right: 18px;
  background: #d9f2fb;
  color: #00a9e0;
}
.delete {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  padding: 10px 5px;
  font-size: 10px;
  cursor: pointer;
}
.placeholder {
  padding: 5px 10px;
  color: #898989;
  font-weight: 700;
}
.placeholder-assignButton {
  color: white !important;
}
.arrow {
  position: absolute;
  top: 5px;
  right: 0px;
  /* right: 5px; */
  display: block;
  padding: 10px 5px;
  font-size: 10px;
  color: #898989;
}
.arrow-assignButton {
  position: absolute;
  top: -3px;
  right: 0;
  /* right: 5px; */
  display: block;
  padding: 10px 5px;
  font-size: 10px;
  color: #fff;
}
.options {
  position: absolute;
  top: 104%;
  left: 0;
  right: 0;
  border: solid #ddd;
  border-width: 0 1px;
  background: #fff;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 100;
  color: #7888a4;
}
.option {
  padding: 10px 15px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}
.option.selected {
  border: 1px solid #00a9e0;
  margin: -1px -1px 0;
  background: #d9f2fb;
}
.option.focused {
  background: #f5f5f5;
}
.checkbox {
  content: "";
  vertical-align: top;
  display: inline-block;
  width: 16px;
  height: 16px;
  padding: 2px;
  border: 1px solid #ddd;
  border-radius: 2px;
  margin: 2px 12px 0 0;
  color: #fff;
  font-size: 10px;
}
.selected .checkbox {
  border-color: #007da6;
  background: #00a9e0;
}
.more-text {
  display: inline-block;
  color: grey;
  margin-bottom: 0;
}
