header {
  display: flex !important;
  padding: 1%;
}
.logo {
  flex: 3;
  text-align: left;
}
.logo img {
  width: 15%;
}
#login {
  flex: 1;
}
#loginbutton {
  background: transparent;
  outline: none;
  border: 2px solid gray;
  color: gray;
  padding: 1% 4%;
  border-radius: 10px;
  font-weight: 600;
  font-size: 17px;
}
hr {
  margin: 0;
  color: #efefef;
}
.dropdown {
  position: relative;
  display: inline-block;
}
