.column {
  width: 23%;
  margin: 1%;
}
.small-font {
  font-size: 15px;
}
.comment-hide {
  color: white;
}
.card {
  padding: 2%;
  margin: 3% 0%;
}
.info-cont,
.more-info {
  display: flex;
}

.img-cont {
  flex: 1;
  padding: 4% 2%;
}
.img-cont img {
  border-radius: 50%;
  width: 150%;
  cursor: pointer;
}
.desc {
  padding-top: 2%;
  margin-left: 15%;
}
.desc a {
  display: block;
  margin-bottom: 2%;
}
.desc p {
  margin: 1% 0%;
  font-size: 13px;
}
.desc .user-name {
  color: #007bff !important;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
}
.desc .full-name {
  color: #007bff !important;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
}
.small-font2 {
  font-size: 11px !important;
}
.desc {
  flex: 3;
}
.more-info {
  margin-top: 4%;
}
