* {
  box-sizing: border-box;
}

.select-box--container {
  border: 2px solid gray;
  border-radius: 5px;
  width: 100%;
  padding: 4px 6px;
  cursor: pointer;
  font-size: 15px;
}

.select-box--box {
  width: 100%;
  position: absolute;
  left: 0;
  color: gray;
  font-weight: 400;
  font-size: 17px;
}

.select-box--arrow {
  color: #898989;
  display: block;
  position: absolute;
  right: 5px;
  top: 5px;
  padding: 10px;
  font-size: 10px;
}
.orderbydropdowncontainer {
  flex: 1;
}
.headerdropdowncontainer {
  display: inline-block;
}
.select-box--selected-item {
  display: inline-block;
  /* height: 100%; */
  /* width: 100%; */
  padding: 4px 5px;
  vertical-align: middle;
}
.orderby-box--selected-item {
  flex: 1;
  /* height: 100%; */
  /* width: 100%; */
  padding: 4px 5px;
  vertical-align: middle;
}
.select-box--items {
  background-color: #f1f1f1;
  min-width: 170px;
  max-height: 400px;
  overflow: auto;
  position: relative;
  z-index: 10000 !important;
  color: black;
  border: solid #ddd;
  border-width: 1px;
  background: #fff;
  font-size: 15px;
  color: #7988a4 !important;
}

.select-box--items div {
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  padding: 10px;
  z-index: 100;
}

.select-box--items div:hover {
  background-color: #f5f5f5;
}
