.tabcontent {
  padding: 0% 1%;
  display: none;
  /* height: 100%; */
}
.img-cont-shortlist {
  padding: 1%;
}
.cat-button-holder button {
  font-size: 12px;
}
.img-cont-shortlist img {
  width: 60%;
  border-radius: 50%;
}
.mid {
  margin: 1% 0%;
}
.item {
  border: 2px solid lightgray;
  border-radius: 10px;
  margin-right: 0px !important;
  margin-top: 2%;
}
.contentbutton {
  display: inline-block;
  background-color: #ddd;
  border: 1px solid #ccc;
  color: black;
  padding: 22px 16px;
  width: 100%;
  margin: 1%;
  outline: none;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
}
.buttons {
  display: flex;
}
.buttons button {
  flex: 1;
}
.contentbutton:hover {
  background-color: gray;
}
.user-name {
  color: #007bff !important;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
}
.full-name {
  color: #007bff !important;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
}
.main-list-heading button {
  float: right;
  background-color: #6c757d;
  border: 1px solid #6c757d;
  border-radius: 4px;
  padding: 5px 8px;
  font-size: 13px;
  color: white;
}
.main-list-heading {
  background-color: #ccc;
  display: inline-block;
  border: 1px solid #ccc;
  color: black;
  padding: 10px 16px;
  width: 100%;
  margin: 1%;
  outline: none;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
}
