.form-cont {
  padding: 2% 2%;
  /* margin-right: 5%; */
}
.select-cont {
  flex: 10;
}
.search-cont {
  margin: auto;
  flex: 1;
}
form {
  display: flex;
  margin-right: 4%;
}

.search-button {
  font-size: 20px;
  color: white;
  background-color: #7988a4;
  border: 2px solid darkblue;
  padding: 5% 3%;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
}
.search-button:hover {
  background-color: darkblue;
}
