* {
  box-sizing: border-box;
}

.box-container {
  border: 2px solid #7988a4;
  width: 100%;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
}

.box {
  width: 200px !important;
  position: relative;
  color: gray;
  font-weight: 600;
  font-size: 17px;
}

.fol-arrow {
  color: #898989;
  display: block;
  position: absolute;
  right: 0px;
  top: 6px;
  padding: 10px;
  font-size: 10px;
}

.fol-filter-box {
  background-color: #f1f1f1;
  overflow: auto;
  z-index: 10;
  color: black;
  position: absolute;
  left: 0;
  top: 100%;
}
.fol-filter-box div {
  padding: 5% 3%;
  overflow: hidden;
  /* margin:auto; */
}
.fol-input {
  width: 39%;
  margin: 3%;
  padding: 4%;
  border: 2px solid gray;
  border-radius: 5px;
}
