.cat-button {
  margin: 0% 1%;
  border: 2px solid #2a416f;
  background-color: #3b5998;
  color: white !important;
  padding: 2px;
  border-radius: 5px;
  font-weight: 600;
  font-size: 10px;
}
.hide {
  display: none;
}
.cat-button-hide {
  margin: 0% 1%;
  border: 2px solid white;
  background-color: white;
  color: white !important;
  padding: 2px;
  border-radius: 5px;
  font-weight: 600;
  font-size: 10px;
}
